import "./styles.css";
import { useState } from "react";
import { Row, Container, Col } from "react-bootstrap";

const App = () => {
     const [mobileBody, setMobileBody] = useState("");

     const onType = (e) => {
          console.debug("typing", e.target.value);
          setMobileBody(e.target.value);
     };

     return (
          <div className="App">
               <Container>
                    <Row>
                         <Col>
                              <h1>Mobile Preview for SMS Editor</h1>
                         </Col>
                    </Row>
                    <Row>
                         <Col>
                              <input type="text" onChange={onType} />
                         </Col>
                         <Col>
                              <div>{mobileBody}</div>
                         </Col>
                    </Row>
               </Container>
          </div>
     );
};
export default App;
